export default {
    name: "load-more",
    props: {
        loadingType: {
            //上拉的状态：0-loading前；1-loading中；2-没有更多了
            type: Number,
            default: 0
        },
        showImage: {
            type: Boolean,
            default: true
        },
        color: {
            type: String,
            default: "#777777"
        },
        contentText: {
            type: Object,
            default: {
                contentdown: "上拉显示更多",
                contentrefresh: "正在加载...",
                contentnomore: "没有更多数据了"
            }
        }
    },
    data() {
        return {}
    }
}
